.home{
	display:flex;
	justify-content: center;
	margin-right: auto;
	margin-left: auto;
	max-width: 1500px;
	background-color: rgb(237,234,234);
}

.home_row{
	display: flex;
	z-index: 1;
	margin-left: 5px;
	margin-right: 5px;
}



.home_img{
   width: 100%;
   height: 500px;
   z-index: -1;
   margin-bottom: -150px;
   mask-image:linear-gradient(to bottom,rgba(0,0,0,1),rgba(0,0,0,0));  
}


